import {
  Button,
  Modal,
  ModalBody,
  ModalContent,
  ModalFooter,
  ModalHeader,
  useDisclosure,
} from "@nextui-org/react";
import {PlusIcon} from "app/assets/icons/Plus";
import {Banner} from "app/components/Banner";
import {ResourceForm} from "app/components/ResourceForm";
import {RelationsTable} from "app/components/ResourcesTable";
import {useResourceServiceCreateResource} from "core/dashboardComponents";
import {useRelation, useSpecs} from "provider/Schema";
import {useNavigate, useParams} from "react-router-dom";
import {resourceKey} from "utils/formatResource";
import {useObject} from "utils/useObject";

export function RelationsPage() {
  const navigate = useNavigate();
  const {resource, key, relation} = useParams();

  const {specs} = useSpecs(relation!);
  const {_from, _to} = useRelation(relation!);

  const {isOpen, onOpen, onOpenChange} = useDisclosure();
  const {value, ...object} = useObject(
    _from === resource
      ? {_from: resourceKey(resource, key), _to}
      : _to === resource
        ? {_from, _to: resourceKey(resource, key)}
        : {}
  );

  const {mutate: createResource, isPending: creatingResource} =
    useResourceServiceCreateResource({
      onSuccess: ({key}) => {
        onOpenChange();
        navigate(`/${relation}/${key}`);
      },
    });

  return (
    <>
      <Modal
        isOpen={isOpen}
        onOpenChange={onOpenChange}
        size="lg"
        scrollBehavior="inside"
        backdrop="blur"
      >
        <ModalContent>
          {(onClose) => (
            <>
              <ModalHeader className="flex flex-col gap-1">
                Create {specs.name}
              </ModalHeader>
              <ModalBody>
                <ResourceForm object={object} specs={specs} />
              </ModalBody>
              <ModalFooter>
                <Button variant="light" onPress={onClose}>
                  Cancel
                </Button>
                <Button
                  color="secondary"
                  variant="flat"
                  onPress={() =>
                    createResource({
                      pathParams: {resource: relation!},
                      body: {data: value},
                    })
                  }
                  isLoading={creatingResource}
                >
                  Create
                </Button>
              </ModalFooter>
            </>
          )}
        </ModalContent>
      </Modal>
      <div className="flex flex-col gap-4 mb-10">
        <Banner
          heading={`${relation}`}
          path={[
            {name: "Home", link: "/"},
            {name: `${resource}`, link: `/${resource}`},
            {name: `${key}`, link: `/${resource}/${key}`},
          ]}
          resource={`${relation}`}
        >
          <Button
            variant="flat"
            color="secondary"
            endContent={<PlusIcon />}
            onClick={onOpen}
          >
            Add {specs.name}
          </Button>
        </Banner>
        <RelationsTable
          resource={resource!}
          _key={key!}
          relation={relation!}
          specs={specs}
        />
      </div>
    </>
  );
}
