import {
  Button,
  ButtonProps,
  LinkIcon,
  Modal,
  ModalBody,
  ModalContent,
  ModalFooter,
  ModalHeader,
  useDisclosure,
} from "@nextui-org/react";
import {BackIcon} from "app/assets/icons/Back";
import {EditIcon} from "app/assets/icons/Edit";
import {Banner} from "app/components/Banner";
import {ResourceForm} from "app/components/ResourceForm";
import {ResourceTable} from "app/components/ResourcesTable";
import {HorizontalScroll} from "app/components/Scroll";
import {
  useResourceServiceDeleteResource,
  useResourceServiceGetResource,
  useResourceServiceUpdateResource,
} from "core/dashboardComponents";
import {useSpecs} from "provider/Schema";
import {StackViewProvider} from "provider/StackView";
import {useCallback, useMemo} from "react";
import {Link, useNavigate, useParams} from "react-router-dom";
import {Element} from "utils/schema";
import {useObject} from "utils/useObject";

export function ResourcePage() {
  const {resource, key} = useParams();
  const {specs, relations} = useSpecs(resource!);

  const {data, isLoading, refetch} = useResourceServiceGetResource({
    pathParams: {
      resource: resource!,
      key: key!,
    },
  });

  // memoize the data table and change on refetch
  const Data = useCallback(
    () => (
      <ResourceTable item={data?.resource} specs={specs} resource={resource!} />
    ),
    [data?.resource, specs, resource!]
  );

  return (
    <div className="flex flex-col gap-4 mb-10">
      <Banner
        heading={`${resource}`}
        path={[
          {name: "Home", link: "/"},
          {name: `${resource}`, link: `/${resource}`},
        ]}
        resource={`${key}`}
      >
        {data ? (
          <UpdateResource
            _key={key!}
            doRefetch={refetch}
            resource={resource!}
            specs={specs}
            data={data.resource}
          />
        ) : (
          <EditResourceButton specs={specs} isDisabled />
        )}
      </Banner>

      {!!relations.length && (
        <HorizontalScroll>
          {relations.map(([edge]) => (
            <RelationLink
              resource={resource}
              _key={key}
              edge={edge}
              key={edge}
            />
          ))}
        </HorizontalScroll>
      )}

      <StackViewProvider
        position="top"
        back={(onBack) => (
          <div>
            <Button
              color="primary"
              variant="light"
              startContent={<BackIcon />}
              onPress={() => onBack()}
            >
              Back
            </Button>
          </div>
        )}
      >
        {!isLoading && data?.resource ? (
          <Data />
        ) : (
          <ResourceTable
            item={{}}
            specs={{...specs, fields: []}}
            loading={isLoading}
            resource={resource!}
          />
        )}
      </StackViewProvider>
    </div>
  );
}

function UpdateResource({
  resource,
  _key,
  specs,
  data,
  doRefetch,
}: {
  specs: Element;
  data: any;
  doRefetch: Function;
  resource: string;
  _key: string;
}) {
  const {value, ...object} = useObject(data);
  const {isOpen, onOpen, onClose, onOpenChange} = useDisclosure();

  const {mutate: updateResource, isPending: updatingResource} =
    useResourceServiceUpdateResource({
      onSuccess: () => {
        doRefetch();
        onClose();
      },
    });

  const navigate = useNavigate();
  const {mutate: deleteResource, isPending: deletingResource} =
    useResourceServiceDeleteResource({
      onSuccess: () => navigate(`/${resource}`),
    });

  return (
    <>
      <Modal
        isOpen={isOpen}
        onOpenChange={onOpenChange}
        size="lg"
        scrollBehavior="inside"
        backdrop="blur"
      >
        <ModalContent>
          {(onClose) => (
            <>
              <ModalHeader className="flex flex-col gap-1">
                Edit {specs.name}
              </ModalHeader>
              <ModalBody>
                <ResourceForm object={object} specs={specs} />
              </ModalBody>
              <ModalFooter>
                <div className="grow">
                  <Button
                    color="danger"
                    variant="light"
                    onPress={() =>
                      deleteResource({
                        pathParams: {
                          resource: resource!,
                          key: _key!,
                        },
                      })
                    }
                    isLoading={deletingResource}
                  >
                    Delete
                  </Button>
                </div>
                <Button color="default" variant="light" onPress={onClose}>
                  Cancel
                </Button>
                <Button
                  color="primary"
                  variant="flat"
                  onPress={() =>
                    updateResource({
                      pathParams: {
                        resource: resource!,
                        key: _key!,
                      },
                      body: {data: value},
                    })
                  }
                  isLoading={updatingResource}
                >
                  Update
                </Button>
              </ModalFooter>
            </>
          )}
        </ModalContent>
      </Modal>
      <EditResourceButton specs={specs} onPress={onOpen} />
    </>
  );
}

function EditResourceButton({specs, ...props}: ButtonProps & {specs: Element}) {
  return (
    <Button color="primary" variant="flat" endContent={<EditIcon />} {...props}>
      Edit {specs.name}
    </Button>
  );
}

function RelationLink({
  resource,
  _key,
  edge,
}: {
  resource: string | undefined;
  _key: string | undefined;
  edge: string;
}) {
  const link = useMemo(
    () => `/${resource}/${_key}/${edge}`,
    [resource, _key, edge]
  );
  return (
    <Link to={link}>
      <Button
        key={edge}
        variant="flat"
        endContent={<LinkIcon />}
        className="capitalize rounded-full mr-2"
      >
        Borrows
      </Button>
    </Link>
  );
}
