import {PropsWithChildren, useCallback, useMemo} from "react";
import {useCookies} from "react-cookie";
import {LoginPage} from "app/pages/LoginPage";
import {jwtDecode} from "jwt-decode";

export function AuthProvider({children}: PropsWithChildren) {
  const {valid} = useAccessToken();
  if (valid) return <>{children}</>;
  return <LoginPage />;
}

export function useAccessToken() {
  const [cookie, setCookie, removeCookie] = useCookies(["auth_token"]);

  const setToken = useCallback(
    (token?: string) => {
      setCookie("auth_token", token);
    },
    [setCookie]
  );

  const removeToken = useCallback(() => {
    removeCookie("auth_token");
  }, [removeCookie]);

  const decoded = useMemo(() => {
    if (!cookie.auth_token) return null;
    return jwtDecode(cookie.auth_token);
  }, [cookie.auth_token]);

  const valid = useMemo(() => {
    if (!cookie.auth_token) return false;
    return decoded?.exp && decoded.exp > Date.now() / 1000;
  }, [cookie.auth_token, decoded]);

  return {token: cookie.auth_token, decoded, valid, setToken, removeToken};
}
