import {PropsWithChildren, createContext, useContext, useMemo} from "react";
import {useSchemaServiceGetSchema} from "core/dashboardComponents";
import {Schema} from "utils/schema";

const SchemaContext = createContext<Schema>({
  edges: {},
  nodes: {},
  relations: {},
});

export const useSchema = () => useContext(SchemaContext);

export const useSpecs = (resource: string) => {
  const schema = useSchema();

  const specs = useMemo(
    () => schema.nodes[resource!] ?? schema.edges[resource!],
    [schema.edges, schema.nodes, resource]
  );

  const relations = useMemo(
    () =>
      Object.entries(schema.relations).filter(([_, {_from, _to}]) =>
        [_from, _to].includes(resource)
      ),
    [schema.relations, resource]
  );

  return {specs, relations};
};

export const useRelation = (relation: string) => {
  const schema = useSchema();
  return schema.relations[relation];
};

export function SchemaProvider({children}: PropsWithChildren) {
  const {data, isLoading} = useSchemaServiceGetSchema({});

  if (isLoading || !data?.data) return <div>loading schema...</div>;

  return (
    <SchemaContext.Provider value={data.data as any}>
      {children}
    </SchemaContext.Provider>
  );
}
