import {useState} from "react";
import {useAuthenticationServiceLogin} from "core/dashboardComponents";
import {useAccessToken} from "provider/Authorized";
import {Button, Card, Image, Input} from "@nextui-org/react";
import logo from "app/assets/images/logo.png";

export function LoginPage() {
  const [email, setEmail] = useState<string>("admin@example.com");
  const [password, setPassword] = useState<string>("5NYDwMlWLnv10WnM");

  const {setToken} = useAccessToken();
  const {mutate: login, isPending} = useAuthenticationServiceLogin({
    onSuccess: (data) => setToken(data.token),
  });

  return (
    <>
      <div className="flex items-center justify-center min-h-screen bg-gray-100">
        <Card className="w-full max-w-md p-8 shadow-lg bg-white animate-moveDown">
          <div className="flex justify-center mb-8">
            <Image src={logo} width={70} height={70} />
          </div>
          <form
            onSubmit={(e) => {
              e.preventDefault();
              login({body: {email, password}});
            }}
          >
            <div className="mb-6">
              <Input
                fullWidth
                placeholder="Email"
                size="lg"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </div>
            <div className="mb-8">
              <Input
                fullWidth
                placeholder="Password"
                type="password"
                size="lg"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <Button
              color="primary"
              fullWidth
              size="lg"
              type="submit"
              disabled={isPending}
            >
              Login
            </Button>
          </form>
        </Card>
      </div>
    </>
  );
}
