import {CheckCircleIcon} from "app/assets/icons/CheckCircleIcon";
import {ClipboardIcon} from "app/assets/icons/ClipboardIcon";
import clsx from "clsx";
import {useCallback, useState} from "react";

export function ClipboardButton({value}: {value: any}) {
  const [copied, setCopied] = useState(false);
  const handleClick = useCallback(() => {
    navigator.clipboard.writeText(value);
    setCopied(true);
    setTimeout(() => setCopied(false), 1000);
  }, [value]);

  return (
    <div
      className={clsx(
        "flex items-center cursor-pointer transition-opacity duration-300 size-5",
        copied ? "opacity-100 text-green-500" : "opacity-30 hover:opacity-100"
      )}
      onClick={handleClick}
    >
      {copied ? <CheckCircleIcon /> : <ClipboardIcon />}
    </div>
  );
}
