import {useSchema} from "provider/Schema";
import {Navigate, useNavigate, useParams} from "react-router-dom";
import {useMemo} from "react";
import {ResourcesTable} from "app/components/ResourcesTable";
import {
  Button,
  Modal,
  ModalBody,
  ModalContent,
  ModalFooter,
  ModalHeader,
  useDisclosure,
} from "@nextui-org/react";
import {PlusIcon} from "app/assets/icons/Plus";
import {Banner} from "app/components/Banner";
import {useObject} from "utils/useObject";
import {ResourceForm} from "app/components/ResourceForm";
import {useResourceServiceCreateResource} from "core/dashboardComponents";

export function ResourcesPage() {
  const navigate = useNavigate();
  const {resource} = useParams();
  const {nodes, edges} = useSchema();

  const specs = useMemo(
    () => nodes[resource!] ?? edges[resource!],
    [edges, nodes, resource]
  );

  const {isOpen, onOpen, onOpenChange} = useDisclosure();
  const {value, ...object} = useObject({});

  const {mutate: createResource, isPending: creatingResource} =
    useResourceServiceCreateResource({
      onSuccess: ({key}) => {
        onOpenChange();
        navigate(`/${resource}/${key}`);
      },
    });

  if (!resource || !specs) return <Navigate to="/" />;

  return (
    <>
      <Modal
        isOpen={isOpen}
        onOpenChange={onOpenChange}
        size="lg"
        scrollBehavior="inside"
        backdrop="blur"
      >
        <ModalContent>
          {(onClose) => (
            <>
              <ModalHeader className="flex flex-col gap-1">
                Create {specs.name}
              </ModalHeader>
              <ModalBody>
                <ResourceForm object={object} specs={specs} />
              </ModalBody>
              <ModalFooter>
                <Button variant="light" onPress={onClose}>
                  Cancel
                </Button>
                <Button
                  color="secondary"
                  variant="flat"
                  onPress={() =>
                    createResource({
                      pathParams: {resource: resource!},
                      body: {data: value},
                    })
                  }
                  isLoading={creatingResource}
                >
                  Create
                </Button>
              </ModalFooter>
            </>
          )}
        </ModalContent>
      </Modal>
      <div className="flex flex-col gap-4">
        <Banner
          heading={resource}
          resource={resource}
          path={[{name: "Home", link: "/"}]}
        >
          <Button
            variant="flat"
            color="secondary"
            endContent={<PlusIcon />}
            onClick={onOpen}
          >
            New {specs.name}
          </Button>
        </Banner>
        <ResourcesTable resource={resource} specs={specs} />
      </div>
    </>
  );
}
